
import {
	defineComponent,
} from 'vue'
import { useLogger } from 'vue-logger-plugin'
import store from '@/store'

export default defineComponent({
	name: 'darkModeSwitch',
	setup() {
		const log = useLogger()

		const toggleDarkMode = () => {
			store.commit('ToggleDarkMode')
			log.debug('dark mode', store.state.common.isDarkMode)
		}
		return {
			log,
			toggleDarkMode,
		}
	},
})
