import store from '@/store'

export default defineComponent({
	components: {
		Header,
		Footer,
		LoadingScreen,
	},
	setup() {
		const isDarkMode = computed(() => store.state.common.isDarkMode)
		// onBeforeMount(async () => {
		// 	console.log('onBeforeMount')
		// })

		// onMounted(() => console.log('mounted app'))
		return {
			isDarkMode,
		}
	},
})
